import React from 'react'
import s from './User.module.css'
import userPhoto from '../../../assets/images/yoda.jpeg'
import { NavLink } from 'react-router-dom'


const User = ({user, followingInProgress, followingThunk, isAuth}) => {

    const onFollow = () => {
        followingThunk('follow', user.id)
    }

    const onUnfollow = () => {
        followingThunk('unfollow', user.id)
    }
    
    
    let followButton = !user.followed ? <button className={s.user__btn}
                                             disabled={!isAuth ? !isAuth : followingInProgress.some(id => id === user.id)}   //массив проверяется на совпадение ID и при true кнопка disabled
                                             onClick={onFollow}
                                             >
                                                Follow
                                            </button> 
                                      : <button className={s.user__btn} 
                                            disabled={!isAuth ? !isAuth : followingInProgress.some(id => id === user.id)}     //массив проверяется на совпадение ID и при true кнопка disabled
                                            onClick={onUnfollow}
                                            >
                                                Unfollow
                                            </button>                             
    return (
        <li className={s.user__item}>
            <span className={s.user__socials}>
                <div className={s.user__image}>
                    <NavLink to={'/profile/' + user.id}>
                        <img src={user.photos.small ? user.photos.small : userPhoto } alt="user-avatar"/>
                    </NavLink>
                </div>
                <div>
                    {followButton}
                </div>
            </span>
            <span className={s.user__content}>
                <span>
                    <div className="user__name">{user.name}</div>
                    <div className="user__status">{user.status}</div>
                </span>
                <span>
                    <div className="user__country">Belarus</div>
                    <div className="user__city">Minsk</div>
                </span>
            </span>
        </li>
    )
}

export default User
