import { connect } from 'react-redux'
import { currentPageActionCreator, followActionCreator, followingThunkCreator, getUsersThunkCreator, toggleFollowingInProgressActionCreator, unfollowActionCreator } from '../../redux/usersReducer'
import React from "react";
import Users from "./Users";
import { getPageSize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress, getUsersSuper } from '../../redux/usersSelectors';
import { getIsAuth } from '../../redux/authSelectors';

class UsersAPIComponent extends React.Component {
    componentDidMount() {
        
        this.props.getUsersThunk(this.props.currentPage, this.props.pageSize)          
    
    }

    onSetCurrentPage = (pageNumber) => {
        this.props.setCurrentPage(pageNumber);

        this.props.getUsersThunk(pageNumber, this.props.pageSize)
    };

    render() {
        
        return (
            <>
                <Users
                    users={this.props.users}
                    totalUsersCount={this.props.totalUsersCount}
                    pageSize={this.props.pageSize}
                    currentPage={this.props.currentPage}
                    onSetCurrentPage={this.onSetCurrentPage}
                    follow={this.props.follow}
                    unfollow={this.props.unfollow}
                    isFetching={this.props.isFetching}
                    followingInProgress={this.props.followingInProgress}
                    toggleFollowingInProgress={this.props.toggleFollowingInProgress}
                    followingThunk={this.props.followingThunk}
                    isAuth={this.props.isAuth}
                />
            </>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsersSuper(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        isAuth: getIsAuth(state)
    }
}

const UsersContainer = connect(mapStateToProps, {
    follow: followActionCreator,
    unfollow: unfollowActionCreator,
    setCurrentPage: currentPageActionCreator,
    toggleFollowingInProgress: toggleFollowingInProgressActionCreator,
    getUsersThunk: getUsersThunkCreator,
    followingThunk: followingThunkCreator
})(UsersAPIComponent)

export default UsersContainer
