import React from "react";
import s from "./Paginator.module.css"

const Paginator = ({ totalUsersCount, pageSize, currentPage, onSetCurrentPage }) => {

  let totalPages = Math.ceil(totalUsersCount / pageSize);

  let pages = [];
  for (
    let i = Math.max(currentPage - 5, 1);
    i <= Math.max(1, Math.min(currentPage + 5, totalPages));
    i++
  ) {
    pages.push(i);
  }

  return (
      <div className={s.pagination}>
        {pages.map((page) => (
          <span
            key={page}
            onClick={() => onSetCurrentPage(page)}
            className={
              currentPage === page
                ? `${s.pagination__item} ${s.selected}`
                : s.pagination__item
            }
          >
            {page}
          </span>
        ))}
      </div>  
  );
};

export default Paginator;
