
import React from "react";
import Preloader from "../Preloader/Preloader";
import Paginator from "./Paginator/Paginator";
import User from "./User/User";
import s from "./Users.module.css";

const Users = ({users, totalUsersCount, pageSize, currentPage, onSetCurrentPage, follow, unfollow, isFetching, followingInProgress, followingThunk, isAuth}) => {

  return (
    <section className={s.users}>
      <div className={s.paginations} >
      <button onClick={() => onSetCurrentPage(1)} >first</button>
        <Paginator
          totalUsersCount={totalUsersCount}
          pageSize={pageSize}
          currentPage={currentPage}
          onSetCurrentPage={onSetCurrentPage}
        />
        <button onClick={() => onSetCurrentPage(Math.ceil(totalUsersCount / pageSize))} >last</button>
      </div>
      
      <h1 className={s.users__title}>Users</h1>
      {isFetching 
            ? <Preloader />
            : <ul className={s.users__list}>
                {users.map(user => (
                  <User
                    key={user.id}
                    user={user}
                    follow={follow}
                    unfollow={unfollow}
                    followingInProgress={followingInProgress}
                    followingThunk={followingThunk}
                    isAuth={isAuth}
                  />
                ))}
              </ul>
      }
    </section>
  );
};

export default Users;
